body {
    font-family: Gilroy, sans-serif;
    font-size: 16px;
    color: white; }

.header {
    padding: 1px;
    background-color: #0b1a2a; }

.header_list {
    display: flex;
    align-items: center;
    margin-left: 339px;
    li {
        color: white;
        font-size: 20px;
        font-weight: semibold;
        list-style-type: none;
        padding-right: 25px;
        a {
            color: inherit;
            text-decoration: none;
            &:hover {
                text-decoration: underline; } } }
    .language {
        padding-left: 70px; }
    .login {
        color: #d1976f;
        border: 1px solid white;
        padding: 8px 40px;
        background: white;
        &:active {
            color: white;
            border: 1px solid white;
            padding: 8px 40px;
            background: #d1976f; } }
    .sign {
        color: white;
        border: 1px solid white;
        padding: 8px 40px;
        background: #d1976f;
        &:active {
            color: #d1976f;
            border: 1px solid white;
            padding: 8px 40px;
            background: white; } } }

.bg_photos {
    min-width: 1300px;
    height: 650px;
    background: url(../img/bg.png) no-repeat;
    -moz-background-size: 100%;
    -webkit-background-size: 100%;
    -o-background-size: 100%; }

.bg {
    padding: 1px;
    min-width: 1300px;
    height: 650px;
    background: url(../img/bg.png) no-repeat;
    -moz-background-size: 100%;
    -webkit-background-size: 100%;
    -o-background-size: 100%; }

.block_regist {
    margin-left: 28%;
    margin-top: 66px;
    width: 410px;
    height: 450px;
    background-color: #0b1a2a;
    border-radius: 20px; }

.regist {
    max-width: 320px;
    margin: 0 auto;
    h3 {
        padding-top: 30px;
        font-size: 20px; } }

label {
    font-size: 15px;
    padding-top: 10px; }

a {
    color: #d0976e; }

input[type="text"], input[type="password"] {
    background-color: #3b6591;
    height: 30px;
    width: 300px; }

input[type="checkbox"] {
    width: 18px;
    height: 18px;
    margin: 10px;
    span {
        display: flex;
        text-align: center; } }

input[type="submit"] {
    margin-top: 10px;
    background-color: white;
    width: 307px;
    height: 40px; }

.payment_systems {
    background: white;
    min-height: 140px; }

.payment_wrapper {
    max-width: 1000px;
    margin: 0 auto;
    display: flex;
    justify-content: space-around;
    align-items: center;
    img {
        padding-top: 23px; } }


.table {
    background-color: #0b1a2a; }


.table_wrapper {}

.table_flex {
    display: flex; }

.table_padding {
    margin-left: 69px;
    ul {
        color: #989898;
        li {
            list-style-type: none;
            padding-bottom: 25px; }
        .active {
            font-size: 20px;
            color: white;
            font-weight: bold; } } }
